@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:border-box}.noselect{-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.no-line-height{line-height:0}.d-none{display:none}.mx-1{margin-inline:1rem}.my-1{margin-block:1rem}.my-05{margin-block:.5rem}.center{display:flex;flex-direction:column;justify-content:center;align-items:center}.small{font-size:.8rem}.text-muted{color:rgba(128,128,128,0.5)}

.draw-container{display:flex;flex-direction:column;align-items:center}.settings{display:flex;width:100%;margin:20px auto;flex-direction:row;justify-content:center;align-items:center}.settings input{margin:5px 0}.vs{display:flex;justify-content:space-evenly;width:100%}.vs .art-container{display:flex;flex-direction:column;align-items:center}.vs .art-container .art-header{font-size:2rem}#pixelart-html,#pixelart-svg,#pixelart-canvas{border:solid 0.1px rgba(0,0,0,0.1)}.canvas{display:flex;flex-direction:column}.cellrow{display:flex}.cellrow .cell{border:solid 0.1px rgba(0,0,0,0.1);cursor:crosshair}.cellrow .cell:hover{background:yellow}.art-header{display:flex}.btn{padding:0.1rem 0.4rem;background:tomato;color:white;border-radius:0.2rem;border:0;font-size:1.2rem}.btn:hover{background:red}.btn-square{width:30px;height:30px;padding:0}.colors{display:flex}.color-history.circle{width:25px;height:25px;margin:0 .5rem;border-radius:50%}

footer{z-index:-1;display:flex;justify-content:center;flex-direction:column}footer .gitIcons,footer .footer-text{text-align:center}footer .gitIcons span{margin:0.5rem 0.5rem}footer{position:absolute;bottom:0;width:100%;color:black;transition:0.5s;border-radius:3px;font-weight:bold}footer .heart{transition:0.5s}footer:hover{color:tomato}footer:hover .heart,footer:hover a{color:tomato}footer a{color:black;text-decoration:none;transition:0.5s}footer a:hover{text-decoration:none}

.see-also{position:fixed;bottom:0;right:0}

header{margin-top:1rem;font-family:'Press Start 2P', cursive;font-size:2rem;text-align:center}

.bmc-button img{height:34px !important;width:35px !important;margin-bottom:1px !important;box-shadow:none !important;border:none !important;vertical-align:middle !important}.bmc-button{position:absolute;top:10px;right:10px;align-items:center;line-height:35px !important;height:51px !important;text-decoration:none !important;display:inline-flex !important;color:#fff !important;background-color:tomato !important;border-radius:5px !important;border:1px solid transparent !important;padding:7px 15px 7px 10px !important;font-size:16px !important;letter-spacing:0.6px !important;box-shadow:0px 1px 2px rgba(190,190,190,0.5) !important;margin:0 auto !important;font-family:'Cookie', cursive !important;box-sizing:border-box !important}.bmc-button:hover,.bmc-button:active,.bmc-button:focus{text-decoration:none !important;box-shadow:0px 1px 2px rgba(190,190,190,0.5) !important;opacity:0.85 !important;color:white !important}

